body{
    font-family: Montserrat;
}
.btn-primary{
    color: #fff !important;
    background: linear-gradient(180deg, #32B1E8 0%, #4898DD 100%) !important;
    box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.12), 0px 2px 2px rgba(0, 0, 0, 0.24) !important;
    border-radius: 2px !important;
     border: none !important;
    padding: auto 4em !important;
}

.main-section{
background-image: url("./images/bg.png");
background-repeat: no-repeat;
background-attachment: fixed;
background-position: right;
min-height: 500px;
background-size: cover;
}
.main-section .header-content {
  padding: 1rem;
  margin: 5rem auto;
  max-width: 460px;
}

.main-section .header-content h2 {

font-family: Montserrat;
font-style: normal;
font-weight: 300;
font-size: 60px;
line-height: 70px;
/* identical to box height, or 117% */

text-transform: capitalize;
}

.team {
    text-align: center;
    padding-top: 4rem ;
    padding-bottom: 4rem ;
}

.games .card{
    line-height: 2rem;
    border: none;
    border-radius: 10px;
    overflow: hidden;
    cursor: pointer;
    padding: 0;
    margin: 3rem;
  }

.games  .card__img img{
  width: 100%;
}

.card__desc{
  text-align: center;
  margin: 2rem auto;
}

@media (max-width : 767px) {

  .main-section .header-content {
    background-color: rgba(252, 249, 249, 0.5); 
}
